/* CTA section styling */

.cta {
  text-align: center;
  background-color: var(--color-primary);
  color: var(--color-light);
  padding: 3rem 1rem;
  border-radius: var(--border-radius);
}

.cta__title {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.cta__subtitle {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta .btn {
  background-color: var(--color-light);
  color: var(--color-primary);
}